import React from 'react';
import Types from 'prop-types';
import classNames from 'classnames';
import CSSTransition from 'react-transition-group/CSSTransition';
import './Slider.less';

export const EXIT_ANIMATION = 600;

const Slider = ({ open, openDirection, children, sliderPositionFixed }) => (
  <CSSTransition
    in={open}
    // Wait for animation to finish before unmount.
    timeout={{ enter: 0, exit: EXIT_ANIMATION }}
    classNames="slider"
    unmountOnExit
  >
    <div
      className={classNames('slider', `slider--open-${openDirection}`, {
        'slider--fixed': sliderPositionFixed,
      })}
    >
      {children}
    </div>
  </CSSTransition>
);

Slider.propTypes = {
  open: Types.bool,
  sliderPositionFixed: Types.bool,
  openDirection: Types.oneOf(['top', 'bottom', 'left', 'right']),
  children: Types.node,
};

Slider.defaultProps = {
  open: false,
  sliderPositionFixed: false,
  openDirection: 'left',
  children: null,
};

export default Slider;



