import React, { Component } from 'react';
import '@transferwise/neptune-css/dist/css/flex.css';
import classNames from 'classnames';

import Slider from './Slider';
import { generateCodeBlock, generateInput, generateState } from '../../docs/utils';

const OPENDIRECTION = [
  { label: 'Right', value: 'right' },
  { label: 'Left', value: 'left' },
  { label: 'Top', value: 'top' },
  { label: 'Bottom', value: 'bottom' },
];

const KNOBS = {
  knobs: [
    { type: 'checkbox', label: 'Open', state: 'open', defaultState: false },
    {
      type: 'checkbox',
      label: 'Position fixed?',
      state: 'sliderPositionFixed',
      defaultState: false,
    },
    {
      type: 'select',
      label: 'Open Direction',
      state: 'openDirection',
      options: OPENDIRECTION,
      defaultState: { ...OPENDIRECTION[0] },
    },
  ],
};

export default class SliderDocs extends Component {
  state = { ...generateState(KNOBS) };
  render() {
    const { open, openDirection, sliderPositionFixed } = this.state;
    const styleContent = {
      width: openDirection.value === 'left' || openDirection.value === 'right' ? 350 : '100%',
      height: openDirection.value === 'top' || openDirection.value === 'bottom' ? 'auto' : '100%',
    };

    return (
      <div className="container">
        <section className="section" id="slider">
          <div className="row">
            <div className="col-md-6">
              <h2>Slider</h2>
              <span role="img" aria-label="uhm">
                Simple slider. The width or the height of the slide are dictate by its content width
                and height.
              </span>
              {generateCodeBlock('Slider', KNOBS, this)}
            </div>
            <div className="col-md-6">
              <div
                className="col-xs-12 m-b-5 p-x-0"
                style={{ height: '300px', overflow: 'hidden', border: '1px solid #d3d5d8' }}
              >
                <Slider
                  open={open}
                  openDirection={openDirection.value}
                  fullParentWidth
                  sliderPositionFixed={sliderPositionFixed}
                >
                  <div
                    className={classNames(
                      'd-flex',
                      'justify-content-around',
                      'align-items-start',
                      'flex-wrap',
                    )}
                    style={{ padding: '20px', ...styleContent }}
                  >
                    <p>
                      Cat ipsum dolor sit amet, purr when being pet. Fight own tail weigh eight
                      pounds but take up a full-size bed fall over dead (not really but gets
                      sypathy) love you, then bite you or sit in box. Spill litter box, scratch at
                      owner, destroy all furniture, especially couch allways wanting food or chase
                      the pig around the house
                    </p>
                  </div>
                </Slider>
              </div>

              <div className="row">{KNOBS.knobs.map(knob => generateInput(knob, this))}</div>
            </div>
          </div>
        </section>
      </div>
    );
  }
}



